#sea-explorer {
position: relative;
}
#sea-explorer .row {  
    position: relative;
}
#sea-explorer .row .col-md-12 {
    padding: 0;  
}



#sea-explorer .row .col-md-12:first-of-type {
    height: 87vh; position: relative; z-index: 10;
    clip-path: polygon(0% 0%, 100% 0%, 100% 80%, 0 100%);
}
#sea-explorer .row .col-md-12:first-of-type .header {
    height: 100%;
}
#sea-explorer .row .col-md-12:first-of-type .header .overlay {
    position: absolute;
    height: 100%; width: 100%; 
    background: linear-gradient(to right bottom, #7dd56f, #28b487);
    opacity: .75;
}

#sea-explorer .row .col-md-12:first-of-type .header img {
    height: 100%; width: 100%; object-fit: cover; object-position: center;
}

#sea-explorer .row .col-md-12:first-of-type .box {
position: absolute; left: 50%; top: 50%;
transform: translate(-50%, -50%);
}

#sea-explorer .row .col-md-12:first-of-type .box > h1 {
    font-size: 3.125rem; text-transform: uppercase;
    width: 70%;  text-align: center;
    margin: 0 auto; color: white; font-weight: 300;
}

#sea-explorer .row .col-md-12:first-of-type .box > h1 span {
    box-decoration-break: clone; padding: .75rem 1.5rem; 
    line-height: 1.5; text-shadow: 0 0.5rem 2rem rgba(0, 0, 0, 0.15);
    background: linear-gradient( to bottom right, rgba(125, 213, 111, 0.85), rgba(40, 180, 135, 0.85) );
}
#sea-explorer .row .col-md-12:first-of-type .box .box-group {
    margin-top: 2rem;
    display: flex; justify-content: center; align-items: center;
}
#sea-explorer .row .col-md-12:first-of-type .box .box-group .box-group-details {
    color: #f7f7f7; font-weight: 700;
}
#sea-explorer .row .col-md-12:first-of-type .box .box-group .box-group-details:first-of-type {
    margin-right: 2rem;
}
#sea-explorer .row .col-md-12:first-of-type .box .box-group .box-group-details i {
    margin-right: .5rem;
}



#sea-explorer .row .col-md-12:nth-of-type(2) {
display: flex; margin-top: -16.5vh;
}
#sea-explorer .row .col-md-12:nth-of-type(2) .overview-box {
    background: #f7f7f7;
    display: flex; justify-content: center; align-items: center; flex: 0 0 50%;
    padding: 14vw 8vw 10vw;
}
#sea-explorer .row .col-md-12:nth-of-type(2) .overview-box > div {
    width: 55%;
}
#sea-explorer .row .col-md-12:nth-of-type(2) .overview-box .overview-box-group h2 {
    text-transform: uppercase; font-size: 1.40625rem; font-weight: 700;
    background: linear-gradient(to right, #7dd56f, #28b487);
    color: transparent; background-clip: text; margin-bottom: 2.25rem;
}
#sea-explorer .row .col-md-12:nth-of-type(2) .overview-box .overview-box-group .overview-box-group-detail {
    margin-bottom: 1.4rem; display: flex; align-items: center; position: relative;
}
#sea-explorer .row .col-md-12:nth-of-type(2) .overview-box .overview-box-group .overview-box-group-detail i {
    color: #55c57a; font-size: 1.35rem;
}
#sea-explorer .row .col-md-12:nth-of-type(2) .overview-box .overview-box-group .overview-box-group-detail span:nth-child(even) {
    text-transform: uppercase; 
    font-weight: 700; font-size: .875rem; 
    position: absolute; left: 15%;
}
#sea-explorer .row .col-md-12:nth-of-type(2) .overview-box .overview-box-group .overview-box-group-detail span:nth-child(odd) {
font-weight: 400; font-size: 0.9375rem;
position: absolute; left: 55%;
}
#sea-explorer .row .col-md-12:nth-of-type(2) .overview-box .overview-box-group:first-of-type {
    margin-bottom: 4.375rem;
}
#sea-explorer .row .col-md-12:nth-of-type(2) .overview-box .overview-box-group img {
    width: 2.1875rem; height: 2.1875rem;
    clip-path: polygon(30% 0%, 70% 0%, 100% 30%, 100% 70%, 70% 100%, 30% 100%, 0% 70%, 0% 30%);
}



#sea-explorer .row .col-md-12:nth-of-type(2) .description-box {
    display: flex; flex: 0 0 50%; flex-direction: column;
    padding: 14vw 8vw 10vw; background: #fcfcfc; 
}
#sea-explorer .row .col-md-12:nth-of-type(2) .description-box h2 {
    text-transform: uppercase; font-size: 1.40625rem; font-weight: 700;
    background: linear-gradient(to right, #7dd56f, #28b487);
    color: transparent; background-clip: text; margin-bottom: 2.25rem;
}
#sea-explorer .row .col-md-12:nth-of-type(2) .description-box .description-text {
    font-size: 1.0625rem; font-weight: 300;
}
#sea-explorer .row .col-md-12:nth-of-type(2) .description-box .description-text:first-of-type {
    margin-bottom: 1.25rem;
}
#sea-explorer .row .col-md-12:nth-of-type(2) .description-box .description-text:last-of-type {
    margin-bottom: 0;
}




#sea-explorer .row .col-md-12:nth-of-type(3) {
    display: flex; position: relative; z-index: 10;
    clip-path: polygon(0 15vh, 100% 0, 100% calc(100% - 15vh), 0 100%);
    margin-top: -14.5vh;  height: 60vh;
}
#sea-explorer .row .col-md-12:nth-of-type(3) .picture-box:first-of-type img {
    padding-top: 1%; display: block;
    width: 100%; height: 100%; object-fit: cover;
}
#sea-explorer .row .col-md-12:nth-of-type(3) .picture-box:nth-of-type(2) img {
    padding-bottom: 1%; display: block;
    width: 100%; height: 100%; object-fit: cover;
}
#sea-explorer .row .col-md-12:nth-of-type(3) .picture-box:nth-of-type(3) img {
    padding-bottom: 5%; display: block;
    width: 100%; height: 100%; object-fit: cover;
}



#sea-explorer .row .col-md-12:nth-of-type(4) {
    display: flex; position: relative; z-index: 10;
    clip-path: polygon(0 15vh, 100% 0, 100% calc(100% - 15vh), 0 100%);
    margin-top: -15vh; 
}
#sea-explorer .row .col-md-12:nth-of-type(4) #map {
    width: inherit !important;
}
#sea-explorer .row .col-md-12:nth-of-type(4) .mapboxgl-marker {
transform: translate(0, 0) !important;
}
#sea-explorer .row .col-md-12:nth-of-type(4) .mapbox-popup .mapboxgl-popup-content {
padding: .6rem !important; text-align: center;
width: 150px; font-size: .75rem;
}
#sea-explorer .row .col-md-12:nth-of-type(4) .mapbox-popup .mapboxgl-popup-tip {
    border-top-color: #55c57a;
}



#sea-explorer .row .col-md-12:nth-of-type(5) { 
    z-index: 10; position: relative; width: 100% !important;  
    clip-path: polygon(0 15vh, 100% 0, 100% calc(100% - 15vh), 0 100%); 
    margin-top: -15vh; padding: 8rem;
    background: linear-gradient(to right bottom, #7dd56f, #28b487);
    display: flex !important; justify-content: center; align-content: center; 
}
#sea-explorer .row .col-md-12:nth-of-type(5) .container {
    display: flex !important; justify-content: center; margin-bottom: 5rem !important;
}
#sea-explorer .row .col-md-12:nth-of-type(5) .container .slick-slider {
     height: 350px; width: 100%; 
     display: flex !important; justify-content: center; align-content: center;
}

#sea-explorer .row .col-md-12:nth-of-type(5) .slick-prev {
    left: -3%; top: 50% !important;
    height: inherit;
  }
  
  #sea-explorer .row .col-md-12:nth-of-type(5) .slick-next {
    right: -3%; top: 50% !important;
    height: inherit;
  }
#sea-explorer .row .col-md-12:nth-of-type(5) .slick-prev::before {
    content: "❮" !important;
    font-size: 2.5rem;
}
#sea-explorer .row .col-md-12:nth-of-type(5) .slick-next::before  {
    content: "❯" !important;
    font-size: 2.5rem;
}
  #sea-explorer .row .col-md-12:nth-of-type(5) .card, #sea-explorer .row .col-md-12:nth-of-type(5) .card-header,
  #sea-explorer .row .col-md-12:nth-of-type(5) .card-body, #sea-explorer .row .col-md-12:nth-of-type(5) .card-footer {
      border: none;
  }
  #sea-explorer .row .col-md-12:nth-of-type(5) .slick-active, #sea-explorer .row .col-md-12:nth-of-type(5) .slick-slide {
    height: 19rem;
  }
  #sea-explorer .row .col-md-12:nth-of-type(5) .slick-track {
    display: flex; justify-content: center; align-content: center;
    position: relative; top: 5%;
  }
  #sea-explorer .row .col-md-12:nth-of-type(5) .slick-active > div, 
  #sea-explorer .row .col-md-12:nth-of-type(5) .slick-slide > div {
    width: inherit; height: inherit;   
  }
  #sea-explorer .row .col-md-12:nth-of-type(5) .col-md-4 {
    width: inherit; height: inherit; max-width: inherit; max-height: inherit;
  }
  #sea-explorer .row .col-md-12:nth-of-type(5) .card {
    width: inherit; height: inherit; 
  }

  #sea-explorer .row .col-md-12:nth-of-type(5) .card-header {
      display: flex; justify-content: space-around; align-items: center;
      padding-top: 1.25rem;
  }
  #sea-explorer .row .col-md-12:nth-of-type(5) .card-header img {
    height: 3rem;
    clip-path: polygon(30% 0%, 70% 0%, 100% 30%, 100% 70%, 70% 100%, 30% 100%, 0% 70%, 0% 30%);
  }
  #sea-explorer .row .col-md-12:nth-of-type(5) .card-header span {
      font-weight: 700; font-size: 0.9375rem; text-transform: uppercase;
  }

  #sea-explorer .row .col-md-12:nth-of-type(5) .card-body {
      background: rgba(0,0,0,.03);
  }
  #sea-explorer .row .col-md-12:nth-of-type(5) .card-body p {
    font-style: italic; font-weight: 300; font-size: 0.9375rem;
  }

  #sea-explorer .row .col-md-12:nth-of-type(5) .card-footer {
      display: flex; justify-content: center; align-items: center;
      padding-bottom: 2rem; padding-top: 0;
  }
  #sea-explorer .row .col-md-12:nth-of-type(5) .card-footer i {
    color: #55c57a;
  }





  #sea-explorer .row .col-md-12:nth-of-type(6) { 
    z-index: 10; background: #f7f7f7; 
    clip-path: polygon(0 15vh, 100% 0, 100% 100%, 0 100%); 
    margin-top: -15vh; padding: 15rem 3rem 10rem;
}

#sea-explorer .row .col-md-12:nth-of-type(6) .cont {
    margin: 0 auto; background: #fff; padding: 5rem 3rem 5rem 16rem;
    border-radius: 1rem; overflow: hidden !important; position: relative;
    box-shadow: 0 3rem 8rem 0.5rem rgba(0, 0, 0, 0.15); width: 60vw;
}

#sea-explorer .row .col-md-12:nth-of-type(6) .cont .cont-img {
    padding: 2rem; transform: translate(-35%, -50%);
    display: flex; justify-content: center; align-items: center;
    background: linear-gradient(to right bottom, #7dd56f, #28b487);
    height: 11rem; width: 11rem; border-radius: 50%;
    position: absolute; left: 0; top: 50%; z-index: 10;
}

#sea-explorer .row .col-md-12:nth-of-type(6) .cont-img > img {
    width: 100%; 
}

#sea-explorer .row .col-md-12:nth-of-type(6) .cont .cont-img ~ img {
    height: 11rem; width: 11rem; border-radius: 50%;
    position: absolute; left: 0; top: 50%;
}
#sea-explorer .row .col-md-12:nth-of-type(6) .cont .cont-img ~ img:first-of-type {
    height: 11rem; width: 11rem; border-radius: 50%;
    position: absolute; left: 0; top: 50%; z-index: 9;
    transform: translate(-10%, -50%) scale(0.97);
}
#sea-explorer .row .col-md-12:nth-of-type(6) .cont .cont-img ~ img:nth-of-type(2) {
    height: 11rem; width: 11rem; border-radius: 50%;
    position: absolute; left: 0; top: 50%; z-index: 8;
    transform: translate(15%, -50%) scale(0.94);
}
#sea-explorer .row .col-md-12:nth-of-type(6) .content {
    display: flex;  justify-content: space-between;  
}
#sea-explorer .row .col-md-12:nth-of-type(6) .content > div {
    display: flex;  flex-direction: column;
}
#sea-explorer .row .col-md-12:nth-of-type(6) .content > div h2 {
    font-size: 1.40625rem; text-transform: uppercase; font-weight: 700;
    background: linear-gradient(to right, #7dd56f, #28b487);
    color: transparent; background-clip: text;
    letter-spacing: 1px; 
   
}
#sea-explorer .row .col-md-12:nth-of-type(6) .content > div p {
    margin: 0; font-size: 1.1875rem; font-weight: 300;
  
}
#sea-explorer .row .col-md-12:nth-of-type(6) .content a {
    background: #55c57a; border-color: #55c57a;
    padding: 1rem 2rem; border-radius: 3rem;
    display: flex; justify-content: center; align-items: center;
}






